.main {
	width: 100%;
	height: 100%;
	overflow: hidden;
	position: relative;

	.list {
		display: flex;
		height: 100%;
		transition: transform 0.4s ease;
	}

	.item {
		min-width: 100%;
		height: 100%;
		background-size: contain;
		background-repeat: no-repeat;
		background-position: center;
	}
}